/* -------------------------------------
 * General Style
 * ------------------------------------- */

 /* -------------------------------------
  * allActivities
  * ------------------------------------- */

 .allActivities{
  max-width: 1200px;
  padding: 0 5%;
  font-family: "Noto Sans", sans-serif;
  color: #77AA90;
  margin-left: 30%;
  padding-top: 5%;
  font-size: 4.5em;
  letter-spacing: 2px;
  text-transform: uppercase;
 }



 /* -------------------------------------
  * timeline
  * ------------------------------------- */
 .timeline {
   list-style: none;
   margin: 5% 0 1% 45%;
   padding-left: 30px;
   border-left: 9px solid #77AA90;
 }
 .timeline >li {
   margin: 40px 0;
   position: relative;
 }
 .timeline >p {
   margin: 0 0 15px;
 }
 
 .date {
  margin-top: -1.5%;
  top: 45%;
  left: -13%;
   font-size: 0.95em;
   line-height: 20px;
   position: absolute;
 }
 
 .circle {
   margin-top: -10px;
   top: 50%;
   left: -39px;
   width: 10px;
   height: 10px;
   background: #ffffff;
   border: 5px solid #ffffff;
   border-radius: 50%;
   display: block;
   position: absolute;
 }
 
 .contents {
   max-height: 20px;
   padding: 50px 20px 0;
   border-color: transparent;
   border-width: 2px;
   border-style: solid;
   border-radius: 0.5em;
   position: relative;
 }
 .contents:before, .contents:after {
   content: "";
   width: 0;
   height: 0;
   border: solid transparent;
   position: absolute;
   pointer-events: none;
   right: 100%;
 }
 .contents:before {
   border-right-color: inherit;
   border-width: 20px;
   top: 50%;
   margin-top: -20px;
 }
 .contents:after {
   border-right-color: #BAD6D6;
   border-width: 17px;
   top: 50%;
   margin-top: -21px;
 }
 .contents >p {
  font-size: 23px;
   max-height: 0;
   color: transparent;
   text-align: justify;
   word-break: break-word;
   -webkit-hyphens: auto;
       -ms-hyphens: auto;
           hyphens: auto;
   overflow: hidden;
 }
 
 .relative >label {
   font-size: 1.3em;
   position: absolute;
   z-index: 100;
   cursor: pointer;
   top: 7%;
   margin-left: -40%;
   margin-right: -13%;
   transition: -webkit-transform 0.2s linear;
   transition: transform 0.2s linear;
   transition: transform 0.2s linear, -webkit-transform 0.2s linear;
 }
 
 .radio {
   display: none;
 }
 
 .radio:checked + .relative >label {
    margin-right: -90%;
   cursor: auto;
   -webkit-transform: translateX(42px);
           transform: translateX(42px);
 }
 .radio:checked + .relative .circle {
  background: #050505;
  border: 5px solid #050505;
 }
 .radio:checked ~ .contents {
   max-height: 180px;
   border-color: #BAD6D6;
   margin-left: 20px;
   -webkit-transform: translateX(-10px);
           transform: translateX(-10px);
   transition: max-height 0.4s linear, border-color 0.5s linear, -webkit-transform 0.2s linear;
   transition: max-height 0.4s linear, border-color 0.5s linear, transform 0.2s linear;
   transition: max-height 0.4s linear, border-color 0.5s linear, transform 0.2s linear, -webkit-transform 0.2s linear;
 }
 .radio:checked ~ .contents >p {
   max-height: 200px;
   color: #77AA90;
   transition: color 0.3s linear 0.3s;
 }
 
/* -------------------------------------
 * mobile phones (vertical version only)
 * ------------------------------------- */
@media screen and (max-width: 767px) {
  .timeline {
    margin-left: 0;
    padding-left: 0;
    border-left: none;
  }
  .timeline li {
    margin: 50px 0;
  }

  label {
    width: 85%;
    font-size: 1.1em;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    display: block;
    -webkit-transform: translateX(18px);
            transform: translateX(18px);
  }

  .contents {
    padding-top: 45px;
    border-color: #eee9dc;
  }
  .contents:before, .contents:after {
    border: solid transparent;
    bottom: 100%;
  }
  .contents:before {
    border-bottom-color: inherit;
    border-width: 17px;
    top: -16px;
    right: 50px;
    margin-left: -17px;
  }
  .contents:after {
    border-bottom-color: #BAD6D6;
    border-width: 20px;
    top: -20px;
    right: 50px;
    margin-left: -20px;
  }
  .contents p {
    font-size: 0.9em;
    line-height: 1.4;
  }

  .circle, .date {
    display: none;
  }

  .allActivities{
    font-size: 1.5em;
   }
   .radio:checked + .relative >label {
    margin-right: -40%;
   cursor: auto;
   -webkit-transform: translateX(42px);
           transform: translateX(42px);
 }
}

